@import '@/sass/index.scss';

























































































.register-container {
  max-width: 300px;
  margin: auto;  


.register-form {
  width: 282px;

  h1 {
    padding: 10px;
    margin-bottom: 10px;
  }

  .register-input {

      label {
        display: block;
        text-align: left;
      }  

      input {
        width: 100%;
        padding: 12px 20px;
        margin: 8px 0;
        display: inline-block;
        border: 1px solid #ccc;
        box-sizing: border-box;
    }
    
  }

  p {
      margin-top: 20px;
      margin-bottom: 20px;
  }
  
}
}

button {
    width: 100%;
    height: 35px;
    margin-top: 10px;
    margin-bottom: 30px;
    background-color: black;
    color: white;
    cursor: pointer;
  }

.registered {
  max-width: 600px;
  margin: auto;
}  

div label.required:after
{
    color: red;
    content: " *";
}

